@import url(https://fonts.googleapis.com/css?family=Orbitron);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


*,
*:before,
*:after {
  box-sizing: border-box;
}

input,
form,
label {
  display: block;
  border: 0;
}

.form-add {
  -moz-appearance: none;
       appearance: none;
  -webkit-appearance: none;
  padding: 10px;
  border: none;
  background-color: #3F51B5;
  color: #fff;
  font-weight: 600;
  border-radius: 5px;
  width: 100%;
}

.form-container {
  margin: 2rem 0;
}

.form {
  display: flex;
  margin: 0 auto;
  justify-content: center;
  align-items: flex-end;
}

.form-field {
  margin: 0 1.5rem 0 0;
}


.form-label {
  margin: 0 0 0.5rem 0;
}

.form-name,
.form-timezone {
  width: 11rem;
  padding: 0.5rem 1rem;
  box-shadow: 0 0 15px 4px rgba(0, 0, 0, 0.06);
  border-radius: 10px;
}
.App-clock {
  font-family: 'Orbitron', sans-serif;
  color: #66ff99;
  font-size: 56px;
  text-align: center;
  padding-top: 40px;
  padding-bottom: 40px;
  padding: 3rem 2rem;
  background-color: #121212;
  margin: 0 0 1rem 0;
  margin-left: auto;
  margin-right: auto;
  width: 45rem;
  border-radius: 3rem;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.clock_remove {
  height: 2rem;
  -moz-appearance: none;
       appearance: none;
  -webkit-appearance: none;
  padding: 10px;
  border: none;
  background-color: #3F51B5;
  color: #fff;
  font-weight: 600;
  display: flex;
  border-radius: 5px;
  width: 2rem;
  justify-content: center;
  align-items: center;
  transition: background-color .15s ease-out;
}

.clock_remove:hover {
  cursor: pointer;
  background-color: #c0d;
}
